const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

// Submit data to Google Sheets
export const submitDataToSheet = async (data) => {
  const response = await fetch(`${API_BASE_URL}/api/submit-to-sheet`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data),
  });
  return response.json();
};

// Save or update patient profile
export const saveProfile = async (profileData) => {
  const response = await fetch(`${API_BASE_URL}/.netlify/functions/api/profile`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(profileData),
  });
  return response.json();
};

// Send chat message and get response
export const sendChatMessage = async (message, patientId) => {
  const response = await fetch(`${API_BASE_URL}/.netlify/functions/api/chat`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ 
      message,
      patientId 
    }),
  });
  return response.json();
};

// Get patient data by ID
export const getPatientData = async (patientId) => {
  const response = await fetch(`${API_BASE_URL}/.netlify/functions/api/patient?patientId=${patientId}`);
  if (!response.ok) {
    throw new Error('Failed to fetch patient data');
  }
  return response.json();
};

// Get chat history for a patient
export const getChatHistory = async (patientId) => {
  const response = await fetch(`${API_BASE_URL}/.netlify/functions/api/chat-history?patientId=${patientId}`);
  if (!response.ok) {
    throw new Error('Failed to fetch chat history');
  }
  return response.json();
};
